<template>
  <div class="Message">
    <h1>ÁïôË®Ä</h1>
    <div class="messageBox">
      <div class="writeBox">
        <el-tabs v-model="activeName">
          <el-tab-pane label="ÁïôË®ÄÊòµÁß∞" name="first">
            <el-input :disabled="NameBol" type="text" placeholder="ËØ∑ËæìÂÖ•ÊòµÁß∞" v-model="nameText" maxlength="10"
              show-word-limit>
            </el-input>
            <span>
              <span class="niming_bol">ÊòØÂê¶ÂåøÂêç</span>
              <el-switch style="margin:30px;" v-model="NameBol" active-color="#13ce66" inactive-color="#ff4949">
              </el-switch>
            </span>
          </el-tab-pane>
          <el-tab-pane label="ÁïôË®ÄÂÜÖÂÆπ" name="second">
            <!-- ËøôÈáåË¶ÅÁî®ÂéüÁîüÁöÑinput ÊâçËÉΩÁî®refÁõëÂê¨Âà∞ÂÖâÊ†á -->
            <input v-model="valueInput" placeholder="ËØ∑ËæìÂÖ•ÁïôË®ÄÂÜÖÂÆπ" ref="inputbox" class="inputSpe" />

            <el-popover placement="bottom" width="200" trigger="click">
              <el-button slot="reference"><b>üòÄ</b></el-button>
              <el-tabs>
                <!-- Â∞ÜË°®ÊÉÖÊ∏≤ÊüìÂà∞ÁªÑ‰ª∂ -->
                <b v-for="(item, index) of emoji" :key="index" @mouseup.capture="handlerEmoji"
                  style="cursor: pointer;user-select: none;">{{ item }}</b>
              </el-tabs>
            </el-popover>

            <!-- </el-input> -->
            <el-button type="primary" plain style="margin:10px;" @click="sendMessage">Áïô‰∏ãÊ∂àÊÅØ</el-button>

          </el-tab-pane>
        </el-tabs>
      </div>
      <messageReply :messageArr='messageArr'  />
    </div>
  </div>
</template>

<script>
//ÂºïÂÖ•vuex
import { mapState } from 'vuex';
import messageReply from './messageChild/messageReply.vue'

export default {
  name: 'Message',
  components:{
    messageReply,
  },
  data() {
    return {
      activeName: 'first',
      //ÊòµÁß∞‰∏éÁïôË®ÄÂÜÖÂÆπ
      nameText: '',
      valueInput: '',
      //ÊòØÂê¶ÂåøÂêçÁöÑbol
      NameBol: false,
      messageArr:[],
      //ÊãøÂà∞ËØ•Êù°ÁïôË®ÄÁöÑid
      // messId:'',
    }
  },
  methods: {
    //ËÆæÁΩÆÁïô‰∏ãÁïôË®ÄÊñπÊ≥ï
    async sendMessage() {
      // console.log(this.valueInput);
      //Ê∂àÊÅØÂÜÖÂÆπÊ£ÄÊµã
      if (!this.valueInput) return this.$message.error('Ê∂àÊÅØÂÜÖÂÆπ‰∏çËÉΩ‰∏∫Á©∫');

      //ÂàõÂª∫‰∏Ä‰∏™Èó≠ÂåÖÔºåÊù•Ëé∑ÂèñÂùóÁ∫ß‰ΩúÁî®ÂüüÂÜÖÁöÑÊï∞ÊçÆ,ËôΩÁÑ∂ËøôÈáå‰∏çÂ§™ÈúÄË¶Å
      async function outer(e) {
        // e => this
        if (e.NameBol) {

          let { data } = await e.$axios.post('/node/message/sendMessage', {
            // visitorName:this.nameText,
            messageValue: e.valueInput
          })
          e.$message.success(data.value)
          // console.log(data.bol);
          return data.bol
        } else {

          let { data } = await e.$axios.post('/node/message/sendMessage', {
            visitorName: e.nameText,
            messageValue: e.valueInput
          })
          if (data.code) {
            e.$message.success(data.value)
            return data.bol
          }
        }
      }

      //Â∞ÜÈó≠ÂåÖËé∑ÂèñÁöÑÊï∞ÊçÆÂ≠òÂÖ•ÂèòÈáèb  
      //ËøîÂõûÁöÑÊòØ‰∏Ä‰∏™ÊúüÁ∫¶ÔºåÂõ†Ê≠§Áî®thenÊìç‰ΩúÂæóÂà∞ÁöÑÊï∞ÊçÆ
      outer(this).then(res=>{
        this.messageArr.unshift(res)
        console.log(this.messageArr,res._id);  //ÂèØ‰ª•ÊãøÂà∞id
        this.messId = res._id
      })
      // console.log(b);
      //ÂèëÈÄÅÂÆåÊ∂àÊÅØÂêéÊ∏ÖÁ©∫input
      this.valueInput = ''
      this.nameText = ''

    },
    //Â∞ÜË°®ÊÉÖËæìÂÖ•ÊñáÊú¨
    handlerEmoji(event) {
      //Ëé∑ÂèñÁÇπÂáªÂæóÂà∞ÁöÑemoji
      let emo = event.target.innerText
      //Ëé∑ÂèñÈº†Ê†áÂÖâÊ†á‰ΩçÁΩÆ
      let inde = this.$refs.inputbox.selectionStart
      // console.log(this.$refs.inputbox.selectionStart);
      //Â∞ÜemojiËæìÂÖ•Âà∞ÂÖâÊ†áÂ§Ñ
      this.valueInput = this.valueInput.substring(0,inde) + emo + this.valueInput.substring(inde)
    },
  },
  computed: {
    //Ëé∑Âèñvuex‰ªìÂ∫ìÊï∞ÊçÆ
    ...mapState(['emoji'])
  },
}
</script>

